<template>
  <div class="card-pbi" :class="{'print-background': printBackground}">
    <div class="card-pbi-item">
      <Resizer :content="`${pbi.id}${pbi.id?':':''} ${pbi.name}`" :max-font-size="35" />
    </div>
    <div class="card-pbi-feature">
      <Resizer
        v-if="pbi.feature"
        :content="`${pbi.feature.id}: ${pbi.feature.name}`"
        :max-font-size="25"
      />
      <Resizer v-else :content="''" :max-font-size="25" />
    </div>
    <div class="card-pbi-team">
      <Resizer :content="pbi.team" :max-font-size="25" />
    </div>
    <div class="card-pbi-effort">
      <Resizer :content="pbi.effort" :max-font-size="25" />
    </div>
    <div class="card-pbi-effort-dev">
      <Resizer :content="pbi.devEffort" :max-font-size="15" />
    </div>
    <div class="card-pbi-effort-test">
      <Resizer :content="pbi.testEffort" :max-font-size="15" />
    </div>
    <div class="card-pbi-sprint">
      <Resizer :content="displaySprint?pbi.sprint:''" :max-font-size="25" />
    </div>
    <div class="card-pbi-notes">
      <Resizer :content="pbi.notes" :max-font-size="25" />
    </div>
  </div>
</template>

 

<script>
import Pbi from "@/models/Pbi";
import Resizer from "./Resizer";

export default {
  name: "PbiCard",
  props: {
    pbi: Pbi,
    displaySprint: Boolean,
    printBackground: Boolean,
  },
  data: () => {
    return {
      styleObject: {
        transform: `rotate(${(Math.random() - 0.5) *
          3}deg) translate(${(Math.random() - 0.5) * 7.5}mm,${(Math.random() -
          0.5) *
          7.5}mm)`
      }
    };
  },
  components: {
    Resizer
  }
};
</script>

 
<style lang="scss">
.card-pbi {
  display: grid;
  grid-template-columns: 25% 25% 15% 7.5% 7.5% 20%;
  grid-template-rows: repeat(7, calc(100% / 7));
  border-top: 1mm solid #000;
  border-left: 1mm solid #000;
  width: 100%;
  height: 100%;

  @media print {
    position: relative;

    &.print-background {

      &:before {
        position: absolute;
        display: block;
        content: '';
        top: -0.5cm;
        left: -0.5cm;
        right: -0.5cm;
        bottom: -0.5cm;
        background-color: #61A2DC;
        -webkit-print-color-adjust: exact;
        z-index: -1;
      }
    }
  }

  > div {
    border-bottom: 1mm solid #000;
    border-right: 1mm solid #000;
    position: relative;
    display: flex;
    justify-content: stretch;
    align-items: center;
    padding: 3.4mm 3mm 1.2mm 2mm;
    font-weight: bold;

    &:after {
      font-size: 2.5mm;
      line-height: 3mm;
      position: absolute;
      top: 0.2mm;
      left: 2mm;
      display: block;
      text-transform: uppercase;
    }
  }

  .card-pbi-item {
    grid-column: 1 / span 6;
    grid-row: 1 / span 3;

    &:after {
      content: "PBI id: name";
    }
  }

  .card-pbi-feature {
    grid-column: 1 / span 6;
    grid-row: 4;

    &:after {
      content: "Feature id: name";
    }
  }

  .card-pbi-team {
    grid-column: 1 / span 2;
    grid-row: 5;

    &:after {
      content: "Team";
    }
  }

  .card-pbi-effort {
    grid-column: 3;
    grid-row: 5;
    border-right: none;

    &:after {
      content: "Effort";
    }
  }

  .card-pbi-effort-dev {
    grid-column: 4;
    grid-row: 5;
    border-right: none;
    padding: 3.4mm 0mm 1.2mm 0mm;

    &:after {
      left: 0mm;
      content: "Dev";
    }
  }

  .card-pbi-effort-test {
    grid-column: 5;
    grid-row: 5;
    padding: 3.4mm 1mm 1.2mm 0mm;

    &:after {
      left: 0mm;
      content: "Test";
    }
  }

  .card-pbi-sprint {
    grid-column: 6;
    grid-row: 5;

    &:after {
      content: "Sprint";
    }
  }

  .card-pbi-notes {
    grid-column: 1 / span 6;
    grid-row: 6 / span 2;

    &:after {
      content: "Notes";
    }
  }
}
</style>