<template>
  <div>
    <div class="controls">
      <!-- <label for="fileinput">Get from file</label> -->
      <!-- <input type="file" @change="mapCsv" id="fileinput" /> -->

    </div>
    <div class="controls">
      <input id="pat" type="text" placeholder="Access token" v-model="pat" />
      <div class="separator" v-if="pat">
        <button v-if="!queries.length" @click="mapQueriesFromAPI">Get all queries</button>
        <select v-if="queries.length" v-model="queryId">
          <option v-for="q in queries" :key="q.id" :value="q.id">{{q.name}}</option>
        </select>
        <button v-if="queryId" @click="mapPBIsFromAPI">Get selected query</button>
      </div>
      
      <div class="separator" v-if="pat">
        <input id="item" type="text" v-model="item" @keyup.enter="addItem"/>
        <button @click="addItem">Add item</button>
      </div>

      <div class="separator">
        <button @click="addPbi">Add empty PBI</button>
        <button @click="addBug">Add empty bug</button>
      </div>

      <div class="separator">
        <button @click="removeAll">Remove all</button>
      </div>

      <div class="stats right">
        <span>Display: </span>
        <span class="stat" @click="displayPbis = !displayPbis">
          <span class="tick"><span v-if="displayPbis"></span></span>
          <span>{{pbis.length}} PBIs</span>
          <span class="red" v-if="(pbis.length%4) && displayPbis">({{-(4-pbis.length%4)}})</span>
        </span>
        <span>,&nbsp;</span>
        <span class="stat" @click="displayBugs = !displayBugs">
          <span class="tick"><span v-if="displayBugs"></span></span>
          <span>{{bugs.length}} Bugs</span>
          <span class="red" v-if="(bugs.length%4) && displayBugs">({{-(4-bugs.length%4)}})</span>
        </span>
        <span>,&nbsp;</span>
        <span class="stat" @click="displaySprint = !displaySprint">
          <span class="tick"><span v-if="displaySprint"></span></span>
          <span>Sprint</span>
        </span>
        <span>,&nbsp;</span>
        <span class="stat" @click="printBackground = !printBackground">
          <span class="tick"><span v-if="printBackground"></span></span>
          <span>Print background</span>
        </span>
      </div>
    </div>
    <div class="home" v-if="displayPbis">
      <card-container :type="'PBI'" v-for="p in pbis" :key="p.key" @remove="removePbi(p.key)">
        <pbi-card :display-sprint="displaySprint" :pbi="p" :print-background="printBackground"/>
      </card-container>
    </div>
    <div class="home" v-if="displayBugs">
      <card-container :type="'Bug'" v-for="b in bugs" :key="b.key" @remove="removeBug(b.key)">
        <bug-card :display-sprint="displaySprint" :bug="b" :print-background="printBackground"/>
      </card-container>
    </div>
    <div id="loading" v-if="loading">Loading...</div>
  </div>
</template>

<script>
// @ is an alias to /src
import PbiCard from "@/components/PbiCard.vue";
import BugCard from "@/components/BugCard.vue";
import CardContainer from "@/components/CardContainer.vue";
import Pbi from "@/models/Pbi.js";
import Bug from "@/models/Bug.js";
import Feature from "@/models/Feature.js";
import Papaparse from "papaparse";

export default {
  name: "home",
  components: {
    PbiCard,
    CardContainer,
    BugCard
  },
  data: () => {
    return {
      loading: false,
      queries: [],
      queryId: "",
      pbis: [],
      bugs: [],
      features: [],
      pat: "",
      item: "",
      displayBugs: true,
      displayPbis: true,
      displaySprint: false,
      printBackground: false
    };
  },
  watch: {
    pat: function (newPat) {
      if (newPat) {
        if (typeof(Storage) !== "undefined") {
          window.localStorage.setItem("pat", newPat);
        }
      }
    }
  },
  created: function() {
    if (!this.pat) {
      if (typeof(Storage) !== "undefined") {
        this.pat = window.localStorage.getItem("pat");
        if (!this.pat) {
          this.pat = ""
        }
      }
    }
  },
  methods: {
    removeAll: function() {
      this.pbis.splice(0)
      this.bugs.splice(0)
    },

    addPbi: function() {
      this.pbis.unshift(new Pbi("", "", "", "", "", "", ""));
    },

    removePbi: function(key) {
      this.pbis.splice(this.pbis.findIndex(p => p.key == key), 1)
    },

    addBug: function() {
      this.bugs.unshift(new Bug("", "", "", "", "", "", ""));
    },

    addItem: async function() {
      if(this.item) {
        this.loading = true;
        await this.getFeaturesFromAPI();
        if (await this.getPBIsFromAPI([this.item])) this.item = ''
        this.loading = false;
      }
    },

    removeBug: function(key) {
      this.bugs.splice(this.bugs.find(b => b.key == key), 1)
    },

    mapCsv: function(evt) {
      {
        const f = evt.target.files[0];
        if (f) {
          const r = new FileReader();
          r.onload = () => {
            const ct = r.result;
            const items = Papaparse.parse(ct);
            for (let i = 0; i < items.data.length; i++) {
              let item = items.data[i];
              if (item[0] === "Feature") {
                let feature = new Feature(item[2], item[3]);
                this.features.push(feature);
              } else if (item[0] === "Product Backlog Item") {
                let pbi = new Pbi(
                  item[2],
                  item[3] === "" ? item[4] : item[3],
                  item[3] === ""
                    ? this.features[this.features.length - 1]
                    : undefined,
                  item[7],
                  item[6],
                  item[5]
                );
                this.pbis.push(pbi);
              }
            }
          };
          r.readAsText(f);
        }
      }
    },

    getJson: async function(url) {
      const res = await fetch(url, {
        headers: {
          authorization: `Basic ${btoa(`:${this.pat}`)}`
        }
      });
      return res.json();
    },

    getQueriesFromAPI: async function() {
      const res = await this.getJson(
        "https://dev.azure.com/shesoftware/Assure/_apis/wit/queries?api-version=5.1&$depth=1"
      );
      this.queries = [
        ...res.value[0].children
          .filter(q => (!q.isFolder && q.queryType == "flat"))
          .map(q => ({
            id: q.id,
            name: q.name
          })),
        ...res.value[1].children
          .filter(q => (!q.isFolder && q.queryType == "flat"))
          .map(q => ({
            id: q.id,
            name: q.name
          }))
      ];
    },

    getWorkItemDetailsBatched: async function(ids, fields) {
      let res;

      if (fields) {
        res = await this.getJson(
          `https://dev.azure.com/shesoftware/Assure/_apis/wit/workitems?ids=${ids.join(
            ","
          )}&api-version=5.1&fields=${fields.join(",")}`
        );
      } else {
        res = await this.getJson(
          `https://dev.azure.com/shesoftware/Assure/_apis/wit/workitems?ids=${ids.join(
            ","
          )}&api-version=5.1&$expand=relations`
        );
      }

      return res.value;
    },

    getFeaturesFromAPI: async function() {
      if (this.features.length) return;
      let res = await this.getJson(
        "https://dev.azure.com/shesoftware/Assure/Assure%20Team/_apis/work/backlogs/Microsoft.FeatureCategory/workItems?api-version=5.1-preview.1"
      );
      const ids = res.workItems.map(wi => wi.target.id);
      const legs = Math.floor(ids.length / 200) + 1;

      for (let i = 0; i < legs; i++) {
        const batch = await this.getWorkItemDetailsBatched(
          ids.slice(200 * i, 200 * (i + 1))
        ); //, [ "System.Title" ]);
        const featureDetails = batch.map(
          f => new Feature(f.id, f.fields["System.Title"])
        );
        this.features.push(...featureDetails);
      }
    },

    getPBIsFromAPI: async function(idArray) {
      let ids = idArray;
      if(!idArray) {
        const res = await this.getJson(
          `https://dev.azure.com/shesoftware/Assure/Assure%20Team/_apis/wit/wiql/${this.queryId}?api-version=5.1`
        );
        if (!res) return false
        ids = res.workItems.map(wi => wi.id);
      }
      const legs = Math.floor(ids.length / 200) + 1;

      if(!ids.length) {
        return false
      }

      for (let i = 0; i < legs; i++) {
        let batch = await this.getWorkItemDetailsBatched(
          ids.slice(200 * i, 200 * (i + 1))
        ); //, [ "System.WorkItemType", "System.Title", "System.AreaPath", "System.IterationPath", "Microsoft.VSTS.Scheduling.Effort", "System.Tags" ]);
        if(!batch) return
        let pbiDetails = batch
          .filter(
            p => p.fields["System.WorkItemType"] === "Product Backlog Item"
          )
          .map(p => {
            const parent = p.relations
              ? p.relations.find(
                  r => r.rel === "System.LinkTypes.Hierarchy-Reverse"
                )
              : undefined;
            let parentId = undefined;
            if (parent) {
              parentId = parent.url.split("/").pop();
            }

            return new Pbi(
              // id, name, feature, team, sprint, effort, notes, devEffort, testEffort
              p.id,
              p.fields["System.Title"],
              parentId
                ? this.features.find(f => `${f.id}` === parentId)
                : parentId,
              p.fields["System.AreaPath"],
              p.fields["System.IterationPath"],
              p.fields["Microsoft.VSTS.Scheduling.Effort"],
              p.fields["System.Tags"] && p.fields["System.Tags"].length
                ? p.fields["System.Tags"]
                : "",
              p.fields["Custom.DevEffort"],
              p.fields["Custom.TestEffort"],
            );
          });
        this.pbis.unshift(...pbiDetails);

        let bugDetails = batch
          .filter(b => b.fields["System.WorkItemType"] === "Bug")
          .map(b => {
            return new Bug(
              // id, name, team, sprint, effort, notes, severity, devEffort, testEffort
              b.id,
              b.fields["System.Title"],
              b.fields["System.AreaPath"],
              b.fields["System.IterationPath"],
              b.fields["Microsoft.VSTS.Scheduling.Effort"],
              b.fields["System.Tags"] && b.fields["System.Tags"].length
                ? b.fields["System.Tags"]
                : "",
              b.fields["Microsoft.VSTS.Common.Severity"],
              b.fields["Custom.DevEffort"],
              b.fields["Custom.TestEffort"],
            );
          });
        this.bugs.unshift(...bugDetails);
      }
      return true;
    },

    mapPBIsFromAPI: async function() {
      this.loading = true;
      await this.getFeaturesFromAPI();
      await this.getPBIsFromAPI();
      this.loading = false;
    },

    mapQueriesFromAPI: async function() {
      this.loading = true;
      await this.getQueriesFromAPI();
      this.loading = false;
    }
  }
};
</script>

<style lang="scss">

.red {
  background-color: #900;
  color: #fff;
  padding: 2px 2px;
  margin: 4px;
  border-radius: 3px;
}

.stat {
  cursor: pointer;
}

.tick {
  width: 18px;
  height: 18px;
  border: 2px solid #333;
  margin: 4px;
  display: inline-block;
  vertical-align: middle;
  position: relative;

  span {
    width:8px;
    height: 8px;
    background:  #333;
    display: inline-block;
    position: absolute;
    top: 3px;
    left: 3px;
  }
}
.home {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  page-break-after: always;
}
.separator {
  margin: 0 0 0 12px;
  border-left: 1px dotted #888;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.controls {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;

  @media print {
    display: none;
  }

  button,
  label,
  select,
  input {
    background-color: transparent;
    border: none;
    padding: 0;
    margin: 0;
    font-size: 10px;
    cursor: pointer;
    margin: 4px 0 4px 12px;
    padding: 4px 12px;
    line-height: 21px;
    border: 2px solid #333;
    text-transform: uppercase;
    font-weight: bold;
    transition: all 0.25s ease-out;

    &:hover {
      background-color: #ccc;
    }
  }

  .stats {
    margin: 0 0 0 12px;
    padding: 0 12px;
    font-size: 10px;
    text-transform: uppercase;
    font-weight: bold;
    align-self: center;

    span {
      vertical-align: middle;
    }
  }

  .right {
    margin-left: auto;
  }

  input[type="file"] {
    width: 0;
    height: 0;
    opacity: 0;
    position: absolute;
    left: -100%;
  }

  input[type="text"], select {
    background-color: white;

    &:hover {
      background-color: #eee;
    }
  }
}

button {
  background-color: transparent;
  border: none;
  padding: 0;
  margin: 0;
  font-size: 14px;
}

#loading {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 100000;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.75);
  color: #fff;
  font-weight: bold;
  text-transform: uppercase;
}
</style>
