<template>
    <div class="container" ref="container">
        <div @input="resize" contenteditable="true" class="body" :style="{fontSize: computedFontSize + 'px' }" ref="body">{{content}}</div>
    </div>
</template>

<script>
export default {
    name: "Resizer",
    props: [
        'content',
        'maxFontSize'
    ],
    data: function () {
        return {
            computedFontSize: this.maxFontSize
        }
    },
    mounted: function () {
        this.$nextTick(this.resize())
    },
    updated: function () {
        this.$nextTick(this.resize())
    },
    methods: {
        resize: function() {
            if(this.$refs.body.clientHeight > this.$refs.container.clientHeight
             || this.$refs.body.clientWidth > this.$refs.container.clientWidth) {
                this.computedFontSize = this.computedFontSize - 0.5;
            }
        }
    }
}
</script>

<style lang="scss" scoped>
    .container {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: stretch;
        align-items: center;
        
        .body {
            min-width: 90%;
            min-height: 90%;
        }
    }
</style>