export default class Pbi {
  constructor(id, name, feature, team, sprint, effort, notes, devEffort, testEffort) {
    this.id = id;
    this.name = name;
    this.feature = feature;
    this.team = team.split("\\").pop();
    if (team === "Assure") {
      this.team = "";
    }
    this.sprint = sprint.split(" ").pop();
    if (!sprint.includes(".")) {
      this.sprint = "";
    }
    this.effort = effort;
    this.notes = notes;

    this.key = id
      ? id + "" + Date.now()
      : Math.floor(Math.random() * 100000) + "" + Date.now();

    this.devEffort = devEffort;
    this.testEffort = testEffort;
  }
}
