<template>
  <article :class="{'bug': (type != 'PBI')}" :style="styleObject">
    <div class="card-container">
      <slot></slot>
    </div>
    <button @click="remove" class="remove">X</button>
  </article>
</template>

 

<script>
export default {
  name: "CardContainer",
  props: ["type"],
  data: () => {
    return {
      styleObject: {
        transform: `rotate(${(Math.random() - 0.5) *
          3}deg) translate(${(Math.random() - 0.5) * 7.5}mm,${(Math.random() -
          0.5) *
          7.5}mm)`
      }
    };
  },
  methods: {
    remove: function() {
      this.$emit("remove")
    }
  }
};
</script>

 

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped lang="scss">

.remove {
  display: block;
  position: absolute;
  top: 1mm;
  right: 1mm;  
  background-color: transparent;
  border: none;
  padding: 0;
  margin: 0;
  font-size: 10px;
  cursor: pointer;
  margin: 0 0 0 0;
  padding: 0;
  line-height: 5mm;
  width: 7mm;
  height: 7mm;
  border: 1mm solid #000;
  text-transform: uppercase;
  font-weight: bold;
  opacity: 0;
  transition: opacity 150ms linear;

  @media print {
    display: none;
  }
}

article {
  padding: 0;
  position: relative;
  width: 148.5mm;
  height: 104.91mm;
  padding: 10mm;
  line-height: 1.1;
  break-inside: avoid;
  border-bottom: 1px dashed #888;
  transition: all 0.5s ease-in-out;

  &:hover {
    .remove {
      opacity: 1;
    }
  }

  &:nth-child(4n + 1) {
    border-right: 1px dashed #888;
  }

  &:nth-child(4n + 3) {
    border-right: 1px dashed #888;
  }

  &.bug {
    @media screen {
      margin: 5mm;
      background-color: rgb(126, 126, 218);
      box-shadow: 0.2mm 0.6mm 0.8mm rgba(0, 0, 0, 0.5),
        0mm 0.2mm 3mm rgba(35, 35, 90, 0.1),
        0.1mm 0.2mm 0.2mm rgba(255, 255, 255, 0.5) inset,
        -0.1mm -0.2mm 0.2mm rgba(0, 0, 0, 0.5) inset;
      background-image: url("../../public/img/red.jpg");
      background-size: 30%;
    }
  }

  @media screen {
    margin: 5mm;
    background-color: rgb(126, 126, 218);
    box-shadow: 0.2mm 0.6mm 0.8mm rgba(0, 0, 0, 0.5),
      0mm 0.2mm 3mm rgba(35, 35, 90, 0.1),
      0.1mm 0.2mm 0.2mm rgba(255, 255, 255, 0.5) inset,
      -0.1mm -0.2mm 0.2mm rgba(0, 0, 0, 0.5) inset;
    background-image: url("../../public/img/blue.jpg");
    background-size: 30%;
  }

  @media print {
    transform: none !important;
  }
}

.card-container {
  width: 100%;
  height: 100%;
}
</style>