<template>
  <div class="card-bug" :class="{'print-background': printBackground}">
    <div class="card-bug-item">
      <Resizer :content="`${bug.id}${bug.id?':':''} ${bug.name}`" :max-font-size="35" />
    </div>
    <div class="card-bug-team">
      <Resizer :content="bug.team" :max-font-size="25" />
    </div>
    <div class="card-bug-effort">
      <Resizer :content="bug.effort" :max-font-size="25" />
    </div>
    <div class="card-bug-effort-dev">
      <Resizer :content="bug.devEffort" :max-font-size="15" />
    </div>
    <div class="card-bug-effort-test">
      <Resizer :content="bug.testEffort" :max-font-size="15" />
    </div>
    <div class="card-bug-sprint">
      <Resizer :content="displaySprint?bug.sprint:''" :max-font-size="25" />
    </div>
    <div class="card-bug-notes">
      <Resizer :content="bug.notes" :max-font-size="25" />
    </div>
    <div class="card-bug-severity">
      <Resizer :content="bug.severity" :max-font-size="25" />
    </div>
  </div>
</template>

 

<script>
import Bug from "@/models/Bug";
import Resizer from "./Resizer";

export default {
  name: "PbiCard",
  props: {
    bug: Bug,
    displaySprint: Boolean,
    printBackground: Boolean,
  },
  data: () => {
    return {
      styleObject: {
        transform: `rotate(${(Math.random() - 0.5) *
          3}deg) translate(${(Math.random() - 0.5) * 7.5}mm,${(Math.random() -
          0.5) *
          7.5}mm)`
      }
    };
  },
  components: {
    Resizer
  }
};
</script>

 
<style lang="scss">
.card-bug {
  display: grid;
  grid-template-columns: 25% 25% 15% 7.5% 7.5% 20%;
  grid-template-rows: repeat(7, calc(100% / 7));
  border-top: 1mm solid #000;
  border-left: 1mm solid #000;
  width: 100%;
  height: 100%;

  @media print {
    position: relative;

    &.print-background {

      &:before {
        position: absolute;
        display: block;
        content: '';
        top: -0.5cm;
        left: -0.5cm;
        right: -0.5cm;
        bottom: -0.5cm;
        background-color: #D85C5C;
        -webkit-print-color-adjust: exact;
        z-index: -1;
      }
    }
  }

  > div {
    border-bottom: 1mm solid #000;
    border-right: 1mm solid #000;
    position: relative;
    display: flex;
    justify-content: stretch;
    align-items: center;
    padding: 3.4mm 3mm 1.2mm 2mm;
    font-weight: bold;

    &:after {
      font-size: 2.5mm;
      line-height: 3mm;
      position: absolute;
      top: 0.2mm;
      left: 2mm;
      display: block;
      text-transform: uppercase;
    }
  }

  .card-bug-item {
    grid-column: 1 / span 6;
    grid-row: 1 / span 3;

    &:after {
      content: "Bug id: title";
    }
  }

  .card-bug-team {
    grid-column: 1 / span 2;
    grid-row: 5;

    &:after {
      content: "Team";
    }
  }

  .card-bug-effort {
    grid-column: 3;
    grid-row: 5;
    border-right: none;

    &:after {
      content: "Effort";
    }
  }

  .card-bug-effort-dev {
    grid-column: 4;
    grid-row: 5;
    border-right: none;
    padding: 3.4mm 0mm 1.2mm 0mm;

    &:after {
      left: 0mm;
      content: "Dev";
    }
  }

  .card-bug-effort-test {
    grid-column: 5;
    grid-row: 5;
    padding: 3.4mm 1mm 1.2mm 0mm;

    &:after {
      left: 0mm;
      content: "Test";
    }
  }

  .card-bug-sprint {
    grid-column: 6;
    grid-row: 5;

    &:after {
      content: "Sprint";
    }
  }

  .card-bug-notes {
    grid-column: 1 / span 6;
    grid-row: 6 / span 2;

    &:after {
      content: "Notes";
    }
  }

  .card-bug-severity {
    grid-column: 1 / span 6;
    grid-row: 4;

    &:after {
      content: "Severity";
    }
  }
}
</style>